<template>
  <v-container fluid class="fill-height">
    <v-row
      align="center"
      justify="center"
    >
      <v-col
        cols="12"
        sm="10"
        md="8"
        lg="6"
        xl="4"
      >
        <v-card class="elevation-12">
          <v-form
            @submit.prevent="signUp"
            ref="form"
            v-model="valid"
          >
            <v-toolbar class="white--text" color="grey darken-3">
              <v-toolbar-title>Sign up</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <v-text-field
                v-model="newUser.username"
                prepend-icon="mdi-account"
                label="Username*"
                :rules="[rules.required, rules.minMaxUsername]"
                required
              ></v-text-field>
              <v-text-field
                v-model="newUser.password"
                :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
                prepend-icon="mdi-lock"
                label="Password*"
                :type="showPass ? 'text' : 'password'"
                :rules="[rules.required, rules.minMaxPass]"
                required
                @click:append="showPass = !showPass"
              ></v-text-field>
              <v-text-field
                v-model="passwordConfirm"
                :append-icon="showPassConfirm ? 'mdi-eye' : 'mdi-eye-off'"
                prepend-icon="mdi-lock"
                label="Repeat password*"
                :type="showPassConfirm ? 'text' : 'password'"
                :rules="[rules.required, rules.passwordMatch]"
                required
                @click:append="showPassConfirm = !showPassConfirm"
              ></v-text-field>
              <v-alert
                v-if="authStatus === 'error'"
                dense
                outlined
                type="error"
              >
                <strong>{{ authError }}</strong>
              </v-alert>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                type="submit"
                class="white--text"
                color="grey darken-3"
              >Sign up</v-btn>
            </v-card-actions>
            <v-divider></v-divider>
          </v-form>
          <p class="text-center font-weight-light">Already have an account? <router-link class="font-weight-bold" to="/sign_in">Sign in</router-link></p>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      newUser: {
        username: '',
        password: ''
      },
      passwordConfirm: '',
      showPass: false,
      showPassConfirm: false,
      valid: false,
      rules: {
        required: v => !!v || 'Required field',
        minMaxPass: v => v.length >= 8 && v.length <= 30 || 'From 8 to 30 characters',
        minMaxUsername: v => v.length >= 5 && v.length <= 30 || 'From 5 to 30 characters',
        passwordMatch: v => v === this.newUser.password || 'Passwords don\'t not match',
      }
    }
  },
  methods: {
    signUp () {
      if (this.$refs.form.validate()) {
        this.$store.dispatch('signUp', this.newUser).then(answer => {
          this.$store.dispatch('getUserCredentials')
          this.$router.push('/')
        }).catch(err => {
          console.log(err)
        })
      }
    }
  },
  computed: {
    authStatus () {
      return this.$store.getters.authStatus
    },
    authError () {
      return this.$store.getters.authError
    }
  },
  beforeRouteLeave (to, from, next) {
    this.$store.state.auth.authStatus = 'none'
    next()
  }
}
</script>
